import { Component, ChangeDetectionStrategy } from '@angular/core';
import { getDateOnly, getRelativeDate } from '@backbase/payment-orders-ang';
import { Ó¨ScheduleDetailsComponent as ScheduleDetailsComponent } from '@backbase/payments-collection-ui-ang';

@Component({
  selector: 'bb-custom-schedule-details',
  templateUrl: './custom-schedule-details.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CustomScheduleDetailsComponent extends ScheduleDetailsComponent {
  constructor() {
    super();
  }

  isToday(startDate: Date | string | number): boolean | void {
    const today = getRelativeDate();
    const date = getDateOnly(new Date(startDate));

    return date === today;
  }
}
